body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/** Now Playing **/

.now-playing__name {
    font-size: 1.5em;
    margin-bottom: 0.2em;
}

.now-playing__artist {
    margin-bottom: 1em;
}

.now-playing__status {
    margin-bottom: 1em;
}

.now-playing__img {
    float: left;
    margin-right: 10px;
    text-align: right;
    width: 45%;
}

.now-playing__img img {
    max-width: 80vmin;
    width: 100%;
}

.now-playing__side {
    margin-left: 5%;
    width: 45%;
}

/** Progress **/

.progress {
    border: 1px solid #eee;
    height: 6px;
    border-radius: 3px;
    overflow: hidden;
}

.progress__bar {
    background-color: #eee;
    height: 4px;
}
html {
  font-size: 1rem;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}


h4 {
  font-family: 'Oswald', sans-serif;
    font-weight: 200;
    font-size: 1.5rem;
}

p {
  font-family: 'Open Sans', sans-serif;
  font-size: 1.125rem;
}

.App-header {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
}

body {
  border-top: .5em white solid;
  background-color: #333;
  background: linear-gradient(-45deg, #ee7752, #ef855d, #0b0d1b, #2f4a44);
  background-size: 250% 280%;
  color: #eee;
  font-family: Helvetica, Arial;
  font-size: 3vmin;
}

body.vibesOn {
  border-top: .5em white solid;
  background: linear-gradient(40deg, #091e1f, #481767);
  background-size: 107% 130%;
  background-attachment: fixed;
}

.body.vibesOff { 
  border-top: .5em white solid;
  background: linear-gradient(-45deg, #ee7752, #ef855d, #0b0d1b, #2f4a44);
  background-size: 250% 280%;
}


.hidden {
  display: none;
}

/** Buttons **/

.btn {
  background-color: transparent;
  border-radius: 2em;
  border: 0.2em solid #1ecd97;
  color: #1ecd97;
  cursor: pointer;
  font-size: 3vmin;
  padding: 0.7em 1.5em;
  text-transform: uppercase;
  transition: all 0.25s ease;
}

.btn:hover {
  background: #1ecd97;
  color: #333;
}

.btn--login {
  margin: 0 auto;
}

/** Background **/

.background {
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-size: cover;
  background-position: center center;
  -webkit-filter: blur(8em) opacity(0.6);
          filter: blur(8em) opacity(0.6);
  position: absolute;
}

.main-wrapper {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  height: 100%;
  margin: 0 auto;
  -webkit-justify-content: center;
          justify-content: center;
  position: relative;
  width: 80%;
  z-index: 1;
} 

.container {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100%;
}

.main-container {
  -webkit-flex: 1 1;
          flex: 1 1;
}

input {
  width: 73%;
    padding: 1em;
    margin: 2em 0;
    border-radius: 5rem;
    border: 0;
}

.welcome-wrapper {
  padding: 1rem;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
}

.welcome-wrapper .welcome-content span { 
  display: -webkit-inline-flex; 
  display: inline-flex;
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 1rem;
}

.welcome-content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
} 

.welcome-content button {
  position: relative;
  top: -1.25rem;
  width: 10em;
  padding: .5em 1em;
  border-radius: .25em;
  border: 0;
}

.welcome-content img {
  height: 3em;
  border-radius: 50%;
  margin-left: 1rem;
}

.leftColumn {
  display: inline-block;
  height: auto;
  width: 100%;
}


.searchedItems-wrapper {
  width: 100%;
  left: 0;
  background: black;
  padding: 5%;
  overflow-x: scroll;
  display: -webkit-flex;
  display: flex;
}

.rightColumn {
  height: auto;
  width: 100%;
  margin-left: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  border-left: 0;
  border-bottom: 1px solid grey;

}


.rightColumn iframe {
  display: block;
 }



.likedTracks-Wrapper, .recentlyPlayed-Wrapper  {
padding: 5%;
}

@media screen and (min-width: 37.500rem) {
  .likedTracks-Wrapper, .recentlyPlayed-Wrapper {
    width: 42%;
    padding: 5% 0;
  }
  .searchedItems-wrapper {
    margin-top: 3em;
  }
}

@media screen and (min-width: 56.250rem) { 
  .App-header {
    -webkit-flex-direction: row-reverse;
            flex-direction: row-reverse;
    -webkit-align-items: baseline;
            align-items: baseline;
  }
  .leftColumn {
    width: 45%;
  }
  .rightColumn {
    width: 50%;
  }
  .likedTracks-Wrapper, .recentlyPlayed-Wrapper {
    width: 100%;
  }
  .searchedItems-wrapper {
    margin-bottom: 2em;
  }
}

